@import '../abstracts/_colors';
@import '../abstracts/_radius';
@import '../abstracts/_mixins';

.LoginForm {
  @include box(center);
  z-index: 1;

  h1 {
    font-weight: 400;
    margin-bottom: 2rem;
  }

  input {
    background-color: $primary-color;
    border: 1px solid $divider-color;
    border-radius: $input-buttons-radius;
    color: $text-icons;
    font-family: Play , sans-serif;
    font-size: 1rem;
    padding: 0.8rem 1.2rem 0.8rem 2.5rem;
  }

  label {
    margin-bottom: 1rem;
    position: relative;
  }

  button {
    @include button($accent-color, 1rem, 100%);
  }  
}

.LoginForm-logo {
  margin-bottom: 2rem;
  width: 200px;
}

.LoginForm-icon {
  align-items: center;
  color: $divider-color;
  display: flex;
  justify-content: center;
  left: 0.8rem;
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
}
